<template>
  <div class="container">
    <el-row>
      <el-col :span="24">
        <el-header class="page-header">
          <h1>榴吨吨员工提成计算辅助</h1>
        </el-header>
      </el-col>
    </el-row>
    <el-row :gutter="20" class="input-row">
      <el-col :span="12">
        <el-date-picker
          v-model="selectedMonth"
          type="month"
          placeholder="选择月份"
          format="yyyy-MM"
          value-format="yyyy-MM"
          @change="resetData"
          class="date-picker"
        ></el-date-picker>
      </el-col>
      <el-col :span="12" class="form-group">
        <el-input
          v-model="newEmployeeName"
          placeholder="输入员工姓名"
          class="employee-input"
        ></el-input>
        <el-button type="primary" @click="addEmployee" class="add-button"
          >添加员工</el-button
        >
      </el-col>
    </el-row>
    <el-row :gutter="20" class="product-group">
      <el-col :span="8">
        <el-input
          v-model="newProductName"
          placeholder="输入产品名称"
          class="product-input"
        ></el-input>
      </el-col>
      <el-col :span="8">
        <el-input-number
          v-model.number="newProductCommission"
          placeholder="输入提成"
          class="product-input"
        ></el-input-number>
      </el-col>
      <el-col :span="8">
        <el-button type="primary" @click="addProduct" class="add-button"
          >添加产品</el-button
        >
      </el-col>
    </el-row>

    <div v-if="selectedMonth" class="sales-data">
      <div v-for="(day, index) in daysInMonth" :key="index" class="day-section">
        <h3>{{ day }}</h3>
        <el-form :model="dailySales[day]" inline class="sales-form">
          <div
            v-for="product in products"
            :key="product.name"
            class="product-input-group"
          >
            <el-form-item :label="product.name">
              <el-input-number
                v-model.number="dailySales[day][product.name]"
              ></el-input-number>
            </el-form-item>
          </div>
          <div
            v-for="(employee, empIndex) in employees"
            :key="empIndex"
            class="employee-checkbox"
          >
            <el-checkbox v-model="attendance[index][empIndex]">
              {{ employee.name }}上班
            </el-checkbox>
          </div>
        </el-form>
      </div>

      <el-button
        type="primary"
        @click="calculateTotalCommission"
        class="calculate-button"
        >计算总提成</el-button
      >

      <el-table :data="employees" style="width: 100%" class="commission-table">
        <el-table-column prop="name" label="员工姓名"></el-table-column>
        <el-table-column label="总提成">
          <template slot-scope="scope">
            {{ scope.row.totalCommission.toFixed(2) }}
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedMonth: "",
      daysInMonth: [],
      dailySales: {},
      attendance: [],
      employees: [],
      newEmployeeName: "",
      products: [],
      newProductName: "",
      newProductCommission: 0,
    };
  },
  watch: {
    selectedMonth(newVal) {
      if (newVal) {
        this.resetData();
      }
    },
  },
  methods: {
    resetData() {
      const startOfMonth = new Date(this.selectedMonth + "-01");
      const endOfMonth = new Date(
        new Date(startOfMonth).getFullYear(),
        new Date(startOfMonth).getMonth() + 1,
        0
      );

      this.daysInMonth = [];
      this.attendance = [];

      for (
        let date = startOfMonth;
        date <= endOfMonth;
        date.setDate(date.getDate() + 1)
      ) {
        const day = `${date.getFullYear()}-${(
          "0" +
          (date.getMonth() + 1)
        ).slice(-2)}-${("0" + date.getDate()).slice(-2)}`;
        this.daysInMonth.push(day);
        this.dailySales[day] = {};
        this.products.forEach((product) => {
          this.dailySales[day][product.name] = 0;
        });
        const dayAttendance = new Array(this.employees.length).fill(false);
        this.attendance.push(dayAttendance);
      }
    },
    addEmployee() {
      if (this.newEmployeeName) {
        this.employees.push({ name: this.newEmployeeName, totalCommission: 0 });
        this.newEmployeeName = "";
        this.updateAttendance();
      }
    },
    addProduct() {
      if (this.newProductName && this.newProductCommission > 0) {
        this.products.push({
          name: this.newProductName,
          commission: this.newProductCommission,
        });
        this.newProductName = "";
        this.newProductCommission = 0;
        this.resetData(); // 重新初始化销售数据
      }
    },
    updateAttendance() {
      this.attendance.forEach((dayAttendance) => {
        dayAttendance.push(false);
      });
    },
    calculateTotalCommission() {
      this.employees.forEach((employee) => {
        employee.totalCommission = 0;
      });

      this.daysInMonth.forEach((day, index) => {
        this.calculateCommissionForDay(day, index);
      });
    },
    calculateCommissionForDay(day, index) {
      let totalCommission = 0;
      const sales = this.dailySales[day];
      this.products.forEach((product) => {
        totalCommission += sales[product.name] * product.commission;
      });

      let workingEmployeesCount = 0;
      this.attendance[index].forEach((attended) => {
        if (attended) {
          workingEmployeesCount++;
        }
      });

      if (workingEmployeesCount === 0) return;

      const commissionPerEmployee = totalCommission / workingEmployeesCount;

      this.attendance[index].forEach((attended, empIndex) => {
        if (attended) {
          this.employees[empIndex].totalCommission += commissionPerEmployee;
        }
      });
    },
  },
};
</script>

<style scoped>
.container {
  padding: 20px;
  background-color: #f3f5f9;
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
}

.page-header {
  background: linear-gradient(45deg, #409eff, #66ccff);
  color: white;
  text-align: center;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.page-header:hover {
  transform: scale(1.05);
}

.page-header h1 {
  margin-top: -20px;
  font-size: 2.5em;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.input-row {
  margin-bottom: 20px;
}

.date-picker,
.employee-input,
.product-input {
  width: 100%;
}

.form-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-group {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sales-data {
  margin-top: 30px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.day-section {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin-bottom: 20px;
}

.employee-checkbox {
  margin-top: 10px;
}

.add-button {
  margin-left: 20px;
}

.calculate-button {
  margin-top: 20px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.commission-table {
  margin-top: 20px;
}

.el-table {
  border-radius: 10px;
}

.el-button {
  border-radius: 5px;
}

.el-checkbox {
  border-radius: 5px;
}
</style>



